.Bio {
  text-align: center;
  align-items: center;
}

.pic {
  height: 30vh;
  padding-top: calc(1px + 1vmin); 
  padding-bottom: calc(1px + 1vmin); 
  pointer-events: none;
  align-items: center;
}

.button{
    border-radius: calc(1px + 1vmin);
	font-size: calc(4px + 2vmin);
	color: black;
    cursor: pointer;
    padding: calc(1px + 1vmin) calc(2px + 1vmin);
    border: none;
	background-color: white;
}

.container{

	font-size: calc(8px + 2vmin);
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
	width: 80vw;
	padding-left: calc(2px + 2vmin);
	padding-right: calc(2px + 2vmin);
	min-height: 100vh;
    align-items: center;
	background-color: #212121;
}
